.carousel {
    margin-top: 5rem;
    margin-right: auto;
    margin-left: auto;
    width: 100%;
  }
  
  .carousel__wrap {
    position: relative;
    background-color: #f5f5f5;
  }
  
  .carousel__viewport {
    width: 100%;
    overflow: hidden;
  }
  
  .carousel__viewport.is-draggable {
    cursor: move;
    cursor: grab;
  }
  
  .carousel__viewport.is-dragging {
    cursor: grabbing;
  }
  
  .carousel__container {
    display: flex;
  }
  
  .carousel__item {
    position: relative; /* Only needed if the carousel option is set to { loop: true } */
    flex: 0 0 80%; /* Slide width will be 80% */
    background-color: #131313;
  
    /* Let's add this temporarily just to test the carousel */
    color: #ffffff;
    height: 300px;
  }

  .carousel__dots {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 1rem;
  }
  
  .carousel__dot {
    display: flex;
    align-items: center;
    background-color: transparent;
    cursor: pointer;
    position: relative;
    padding: 0;
    width: 3rem;
    height: 3rem;
    margin-right: 0.75rem;
    margin-left: 0.75rem;
    border: 0;
  }
  
  .carousel__dot:after {
    background-color: #efefef; /* Default dot color */
    width: 100%;
    height: 0.4rem;
    content: "";
  }
  
  .carousel__dot.is-selected:after {
    background-color: #1bcacd; /* Selected dot color */
  }

  .carousel__arrowBtn {
    background-color: transparent;
    position: absolute;
    transform: translateY(-50%);
    top: 50%;
    width: 5rem;
    height: 5rem;
    padding: 0;
    border: 0;
    cursor: pointer;
    fill: #1bcacd; /* Enabled color */
  }
  
  .carousel__arrowBtn:disabled {
    fill: #e9e9e9; /* Disabled color */
    opacity: 0.5;
  }
  
  .carousel__arrowBtn--prev {
    left: calc(5% - 2.5rem);
  }
  
  .carousel__arrowBtn--next {
    right: calc(5% - 2.5rem);
  }
  
  .carousel__arrowBtn__svg {
    width: 3.5rem;
    height: 3.5rem;
  }